			display: flex;
			justify-content: center;
			align-items: center;
			flex-flow: center;
			background: #fff;
			border: 1px solid #e02522;
			border-right: none;
			text-decoration: column nowrap;
			position: relative;
			height: 40px;
			padding-left: 10px;
			border-radius: 5px;
			width: calc(100% / 5 - 15px);
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;

			@include media(pc) {
				border-radius: 10px;
				width: calc(100% / 5 - 38px);
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
				height: 60px;
				padding-left: 15px;
			}

			&::after {
				content: " ";
				position: absolute;
				display: inline-block;
				background-image: url(../images/online/online_flow_bg_basic.png);
				background-size: contain;
				vertical-align: middle;
				background-repeat: no-repeat;
				height: 40px;
				right: -24px;
				width: 24px;
				top: -1px;

				@include media(tb) {
					right: -22px;

				}

				@include media(pc) {
					height: 60px;
					right: -21px;
					width: 21px;
					top: -1px;
					background-image: url(../images/online/online_flow_bg_basic-pc.png);
				}
			}

			//after
